import React, { Component } from 'react';
import './App.css';
import YouTube from 'react-youtube';

export default class Table extends React.Component {
    constructor(props) {
       super(props)
       this.state = {
          navigations: [
             { id: 1, transcript: 'Wasif', subtitle: 21, frame: 'wasif@frame.com' },
             { id: 2, transcript: 'Ali', subtitle: 19, frame: 'ali@frame.com' },
             { id: 3, transcript: 'Saad', subtitle: 16, frame: 'saad@frame.com' },
             { id: 4, transcript: 'Asad', subtitle: 25, frame: 'asad@frame.com' }
          ]
       }
    }
 
    renderTableHeader() {
       let header =['id', 'transcript', 'subtitle', 'frame']
       return header.map((key, index) => {
          return <th key={index}>{key.toUpperCase()}</th>
       })
    }
 
    renderTableData() {
       return this.state.navigations.map((navigation, index) => {
          const { id, transcript, subtitle, frame } = navigation //destructuring
          return (
             <tr key={id}>
                <td>{id}</td>
                <td>{transcript}</td>
                <td>{subtitle}</td>
                <td>
                    {/* <YouTube
                        className="youtube-video"
                        videoId={this.props.videoID}
                        onPause={this._onPause}
                        onPlay={this._onPlay}
                        onReady={this._onReady}
                        onStateChange={this._onStateChange}
                        opts={{playerVars: {autoplay: 1, start: {frame}, end: {frame} + 10}}}
                        /> */}
                        {this.props.videoID}
                        </td>
             </tr>
          )
       })
    }
 
    render() {
       return (
          <div>
             <table id='navigations'>
                <tbody>
                   <tr>{this.renderTableHeader()}</tr>
                   {this.renderTableData()}
                </tbody>
             </table>
          </div>
       )
    }
 }
 
